@import "_variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('../#{$icomoon-font-path}/#{$icomoon-font-family}.eot?ln899j');
  src:  url('../#{$icomoon-font-path}/#{$icomoon-font-family}.eot?ln899j#iefix') format('embedded-opentype'),
    url('../#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?ln899j') format('truetype'),
    url('../#{$icomoon-font-path}/#{$icomoon-font-family}.woff?ln899j') format('woff'),
    url('../#{$icomoon-font-path}/#{$icomoon-font-family}.svg?ln899j##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrow {
  &:before {
    content: $icon-arrow;
    color: #333;
  }
}
.icon-burger {
  &:before {
    content: $icon-burger;     
    color: #666;
  }
}
.icon-search {
  &:before {
    content: $icon-search; 
  }
}

